body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,h2,h3,h4{
  font-family: 'Hammersmith One', sans-serif;
  color:wheat;
}

.banner-children{
  display:flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
    position: relative;
}
.parallax-inner,.banner-image{
  display: 100vh;
}

.myNav{
  background-color: #692027;
}

.socialIcon{
  max-width: 70px;
  margin:3px;
}

.carousel .item img{
  width:100%;
}

.Gallery .carousel{
  max-height: 70vh;
}
.Gallery .carousel-inner{
  max-height: 70vh;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media (min-width:900px){
  .App{
    margin: 0 10vw;
  }
}

.Heading{
    margin: 0;
    height: 100vh;
    background-image:url(/Coffee-Shop-React/static/media/concrete1.53dcb35b.jpg);
    background-size:cover;
    display:flex;
    justify-content:center;
    align-items:center;
}

.Heading-Logo{
    height: 40vh;
    width: 100%;
    display:flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
}

.Slogan{
    font-family: monospace;
    font-weight: bold;
    margin:0;
    color:wheat;
}

.Logo{
    width:25vw;
    min-width: 200px;
    margin: 20px 20px 0 20px;
}
.banner-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.banner-image {
    width: 100vw;
    height: 100vh; 
    background-size: cover;
    /* background-position: center; */
}

.banner-children {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    
    display: flex;
    align-items: center;
    justify-content: center;
}

    .myNav{
        margin: 0;
    }

    @media (min-width: 992px){
        .myNav .container{
            width:100%;
        }
    }


.About{
    height: 130vh;
    margin: 0;
    color:white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.About h1{
    margin: 0;
    color: wheat;
    font-family: 'Hammersmith One', sans-serif;
}

.About-text{
    max-width: 40vw;
    min-width: 300px;
    font-size: calc(10px + 1vmin);
}

.About-pic1,.About-pic2{
    width: 100%;
    height: 25vh;
}
.About-pic1{
    background-image: url(/Coffee-Shop-React/static/media/inside.2d431135.JPG);
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: -100px;
}
.About-pic2{
    background-image: url(/Coffee-Shop-React/static/media/PlaceOutsideFront.21327946.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: -30px;
}

.About-text-container{
    background-color: #52191f;
    padding: 20px;
    height: 80vh;
    width: 100%;
    text-align: start;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (min-width:460px){
    .About-text{
        font-size: calc(10px + 0.8vmin);
        min-width: 150px;
    }
}
@media (min-width:600px){
    .About-text-container{
        flex-direction: row;
        justify-content: space-between;
        text-align: center;
        margin: 0 15px;
    }
}

.Quote{
    height:50vh;
    width: 100%;
